

#registration-page {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    background: linear-gradient(
    to left,
        rgba(7, 27, 82, 1) 0%,
        rgba(0, 128, 128, 1) 100%
    );
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 12px;
}

form input {
    padding: 6px;
    outline: none;
    font-size: 18px;
    border-radius: 6px;
    backdrop-filter: blur(100000%);
    border: 2px solid rgba(255, 255, 255, 0.672);
    color: white;
    background-color: transparent;
}

form #failSpan {
    color: red;
    opacity: 0;
}