

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 10%;
}

nav ul {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
}

#nav-right-part {
    width: 14%;
    display: flex;
    justify-content: center;
}

button {
    padding: 3px 12px;
}